.App {
  font-family: sans-serif;
  text-align: center;
}

[class*="ymaps-2-1"][class*="-ground-pane"] {
  /*filter: grayscale(1);*/
  /*-ms-filter: grayscale(1);*/
  /*-webkit-filter: grayscale(1);*/
  /*-moz-filter: grayscale(1);*/
  /*-o-filter: grayscale(1);*/
}

[class*="-balloon"] {
  border-radius: 10px;
  background-color: #ffffff;
}

.map-container {
  margin: 0;
  padding: 0;
  max-width: 100%;
  height: 100%;
  background-color: grey;
  border-radius: 20px;
  overflow: hidden;
}

.map__link {
  color: #122023;
  font-size: 15px;
  text-decoration: none;
}

.map__image {
  border-radius: 10px;
  position: relative;
  width: 100%;
  z-index: 1000;
}

.map__button {
  margin-top: 2px;
  display: flex;
  width: 135px;
  padding: 10px 32px;
  justify-content: center;
  align-items: center;
  border: 2px solid black;
  border-radius: 32px;
  text-decoration: none;
  color: black;
  transition: 0.3s opacity ease;
}

.map__button:hover {
  opacity: 0.7;
}
